@import "../../styles/responsive/breakpoints.scss";

.landing {
	display: flex;
	align-items: center;
	min-height: 100vh;
	justify-content: space-around;
	max-width: 100vw;

	@include screenxs {
		flex-direction: column-reverse;
		justify-content: center;
		align-items: center;
	}

	@include screenm {
		flex-direction: column-reverse;
		justify-content: center;
		align-items: center;
	}

	.landing-title {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;

		@include screenxs {
			text-align: center;
			justify-content: center;
		}

		.landing-title-text {
			display: flex;
			flex-direction: column;
			align-items: center;

			.landing-title-text-name {
				font-size: 9vw;
				font-weight: 900;
				transition: all 0.5s ease-in-out;
				overflow: hidden;
				margin: 0;

				@include screenxs {
					font-size: 80px;
				}

				span {
					margin: 0;
					display: inline-block;
				}
			}
			.landing-title-text-title {
				font-size: 23px;
				margin: 0;
			}
		}

		.landing-title-cta-buttons {
			margin-top: 5em;
			display: flex;

			@include screenxs {
				margin-top: 3em;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
			}
			@include screenm {
				margin-top: 3em;
				display: flex;
				justify-content: center;
				align-items: center;
				flex-direction: row;
			}

			.landing-title-cta-contact-me {
				cursor: pointer;
				display: flex;
				justify-content: center;
				align-items: center;
				color: white;
				margin-right: 15px;
				height: 50px;
				margin-top: 1em;
				border: 3px solid white;
				width: 200px;
				border-radius: 30px;
				background: none;
				transition: all 0.3s ease-in;
				text-decoration: none;

				&:hover {
					color: black;
					background: white;
					a {
						color: black;

						text-decoration: none;
					}
				}

				a {
					text-decoration: none;
				}
			}

			.landing-title-cta-my-work {
				cursor: pointer;
				display: flex;
				justify-content: center;
				align-items: center;
				color: white;
				margin-right: 20px;
				height: 50px;
				border-radius: 30px;
				width: 200px;
				border: none;
				background: linear-gradient(142deg, #e99a33 0%, #be3490 100%);
				transition: all 0.3s ease-in;
				text-decoration: none;
				margin-top: 1em;

				&:hover {
					background-color: white;
					color: black;
				}
			}
		}
	}
	.landing-icon {
		@include screens {
			margin-bottom: 2em;
		}

		@include screenm {
			margin-bottom: 2em;
		}

		@include screenl {
			margin-bottom: 2em;
		}

		.landing-icon-first {
			transform: translate(-50px, -50px);
			position: absolute;
			width: 150px;
			height: 150px;
			background: none;
			border: 1px solid white;
		}
		.landing-icon-second {
			width: 150px;
			height: 150px;
			background: none;
			border: 1px solid white;
		}
	}

	.read-more {
		cursor: pointer;
		position: absolute;
		bottom: 1em;
		display: flex;
		flex-direction: column;
		align-items: center;

		animation: upanddown 3s ease-in-out infinite;

		@keyframes upanddown {
			0% {
				transform: translateY(0px);
			}
			50% {
				transform: translateY(-20px);
			}
			100% {
				transform: translateY(0px);
			}
		}

		.read-more-text {
			margin-bottom: 3px;

			@include screens {
				display: none;
			}
			@include screenm {
				display: none;
			}
			@include screenl {
				display: none;
			}
		}

		.read-more-line {
			width: 1px;
			height: 30px;
			background-color: white;
		}
	}
}
